@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Source Sans Pro", Arial, sans-serif;
}

.movie-card {
  display: flex;
  flex-direction: row;
  border: 0px solid #ffffff;
  border-radius: 10px;
  margin: 3em 50px;
  box-shadow: 0px 0px 10px #ffffff;
  max-height: 150px;
}

.movie-description {
  padding: 1.9em 1em;
}
.movie-description h1 {
  font-size: 1.5em;
  line-height: 0em;
  margin-bottom: 0.5em;
}
.movie-description span#movie-release-date-year {
  color: lightgray;
  font-style: italic;
}
.movie-description p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* number of lines to show */
  -webkit-box-orient: vertical;
  margin-top: 10px;
}

#loader {
  display: none;
}
.no-image-poster {
  background-image: url("https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-38-picture-grey-c2ebdbb057f2a7614185931650f8cee23fa137b93812ccb132b9df511df1cfac.svg");
  background-color: #dbdbdb;
  background-size: 50px !important;
  background-position: center;
  background-repeat: no-repeat;
  color: lightgray;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  border-radius: 10.5px;
}
.no-image-poster-search {
  border-radius: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.search-movie-poster {
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  /* background: black; */
  min-width: 100px;
  min-width: 100px;
  height: 150px;
  background-size: cover;
  background-size: 100%;
  background-position: center;
  transition-duration: 0.3s;
}
.search-movie-poster:hover {
  background-size: 105%;
}
/* //--------------------------------------------------------- */
.movie-list {
  display: flex;
  flex-direction: column;
  margin: 0 10px 20px 10px;
}
.movie-list .movie-list-description {
  margin: 10px 10px 0 10px;
  color: lightgray;
}
.movie-list h1 {
  margin-bottom: 5px;
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
}
.movie-image {
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  border-radius: 10px;
  background: black;
  background-position: center;
  /* min-width: 100px;
  min-width: 100px;
  max-width: 150px; */
  width: 150px;
  height: 225px;
  background-size: 150px;
  transition-duration: 0.3s;
}
.movie-image:hover {
  background-size: 155px;
}

#most-popular-movies {
  margin: 2em 0 0 0;
}
#most-popular-movies h1 {
  margin-bottom: 0.5em;
}
#most-popular-movies section {
  display: flex;
  overflow-x: scroll;
}

#most-trending-movies {
  margin: 2em 0 0 0;
}
#most-trending-movies h1 {
  margin-bottom: 0.5em;
}
#most-trending-movies section {
  display: flex;
  overflow-x: scroll;
}

.click-movie-info {
  cursor: pointer;
  text-decoration: none;
}
.click-movie-info:hover {
  text-decoration: none;
}

/* ------------------------------------------------------------------- */
#movie-details-backdrop {
  max-width: 100vw;
  height: 600px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
#movie-details-backdrop #custom-bg{
  background: linear-gradient(90deg, rgba(29,29,29,1) 0%, rgba(200,200,200,0.5) 100%);
  height: inherit;
}
#movie-details-backdrop #custom-bg .container{
  display: flex;
  flex-direction: row;
  align-items: center;
  height:inherit;
  padding: 3.3em;
}

#movie-details-no-image-poster {
  background-image: url('https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-38-picture-grey-c2ebdbb057f2a7614185931650f8cee23fa137b93812ccb132b9df511df1cfac.svg');
  background-color: #dbdbdb;
  background-size: 15em;
  background-position: center;
  background-repeat: no-repeat;
  color: lightgray;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  border-radius: 10.5px;
}

#movie-details-movie-image {
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  border-radius: 10px;;
  background-position: center;
  /* min-width: 100px;
  min-width: 100px;
  max-width: 150px; */
  width: 342px;
  height: 513px;
  background-size: 342px;
  transition-duration: 0.3s;
}
#movie-details-movie-image:hover {
  background-size: 347px;
}

#movie-details-backdrop #custom-bg .container section{
  align-self: flex-start;
  margin-left: 2em;
}
#movie-details-backdrop #custom-bg .container section h1 {
  font-weight: 700;
  font-size: 3em;
  margin: 0;
}
#movie-details-backdrop #custom-bg .container section h1 span{
  font-weight: 600;
  color: #dbdbdb;
}
#movie-details-backdrop #custom-bg .container section p {
  font-size: 1.2em;
  margin-top: 0;
  line-height: 1.3em;
}
#movie-details-backdrop #custom-bg .container section h2{
  font-weight: 600;
  font-size: 1.6em;
  margin-bottom: 2px;
}

/*---------------------------------------------------------------*/
#movie-top-cast-title {
  margin: 0;
  font-weight: 700;
  font-size: 2em;
}
#movie-top-cast-cards {
  display: flex;
  margin-bottom: 100px;
  overflow-x: scroll;
  overflow-y: none;
}
#movie-top-cast-cards .card {
  border-radius: 10px;
  min-width: 140px;
  width: 140px;
  min-height: 235px;
  margin: 25px 20px 25px 0;
}
#movie-top-cast-cards .card-body {
  padding: 0;
  padding: 10px;
}
#movie-top-cast-cards .card .card-img-top {
  border-top-left-radius: 9px;
  border-top-right-radius: 9px;
  height: 175px;
  margin: 0;
}
#movie-top-cast-cards .card-body h1 {
  font-size: 1.1em;
  font-weight: 700;
  margin: 0;
}
/*-----------------------------------------------------*/
movie-facts section {
  margin: 2em 0;
}
movie-facts section h1 {
  font-size: 1.3em;
  font-weight: 700;
  margin: 0;
}
movie-facts section p {
  font-size: 1.2em;
  margin: 0;
}
/*-----------------------------------------------------*/
.btn-imdb {
  font-size: 2em;
  font-weight: 800;
  color: black;
  line-height: 0;
  padding: 0px 10px;
  background-color: #f3ce13;
  border-radius: 5px;
  transition: 0.3s;
}
.btn-imdb:hover {
  text-decoration: none;
  color: black;
  opacity: 0.7;
}

/*----------------------*/
.search-form-section {
  background-color: white;
  position: absolute;
  width: 100%;
  height: auto;
  height: 50px;
  display: none;
}
.search-form-section .form-group{
  display: flex;
  flex-direction: row;
  align-items: center;
}
#search-form-icon {
  color: black;
  font-size: 20px;
  width: 1em;
}
#search-form-input {
  border: none;
  height: 50px;
}
#search-form-input:focus {
  box-shadow: none;
}

/*-----------*/
nav-footer {
  display: none;
}

/*----------------------*/
#movie-details-phone {
  display:none;
}

/*---------------------------------*/

/*-----------------------*/
@media only screen
  and (min-device-width: 375px)
  and (max-device-width: 667px)
  and (-webkit-min-device-pixel-ratio: 2) {
  /*/-------------------------------------*/
  #movie-top-cast-cards {
    margin: 0;
  }
  #movie-topcast-facts {
    display: flex;
    flex-direction: column;
  }
  #movie-topcast-facts .col-9 {
    min-width: 100vw;
    height: inherit;
  }
  #movie-topcast-facts .col-3 {
    min-width: 100vw;
  }
  /*/-------------------------------------*/
  #movie-details-no-image-poster {
    background-color: #dbdbdb;
    background-size: 5em;
    background-position: center;
    width: 100px;
    height: 150px;
    background-repeat: no-repeat;
    color: lightgray;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    border-radius: 10.5px;
  }

  #movie-details-movie-image {
    width: inherit;
    height: inherit;
    border-top-left-radius: inherit;
    border-bottom-left-radius: inherit;
    border-radius: 10px;;
    background-position: center;
    background-size: cover;
  }
  #movie-details-movie-image:hover {
    background-size: 100%;
  }
  /*--------------------------------------------------------------------*/
  #movie-details-backdrop {
    width: 100vw;
    height: 200px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  #movie-details-backdrop #custom-bg{
    background: linear-gradient(90deg, rgba(29,29,29,1) 0%, rgba(200,200,200,0.5) 100%);
    height: inherit;
  }
  #movie-details-backdrop #custom-bg .container{
    display: flex;
    flex-direction: row;
    align-items: center;
    height:inherit;
    padding: 3.3em;
  }


  /*=-------------------------------------------------*/
  footer .container {
    justify-content: center !important;
  }
  footer .container div {
    margin-bottom: 10px;
  }
  footer .container img {
    width: 8em !important;
  }
  .movie-card {
    margin-left: 0;
    margin-right: 0;
  }
  .movie-description h1 {
    line-height: 1em
  }
  .movie-description p span {
    display: none;
  }
}

/*----------------------------------------*/
@media only screen and (max-device-width: 800px) {
  #movie-details-backdrop #custom-bg .container section{
    display: none;
  }
  #movie-details-phone {
    display: block;
  }
  #movie-details-phone h1{
    text-align: center;
    font-size: 2em;
    margin-top: 0.3em;
    font-weight: 700;
  }
  #movie-genre-runtime {
    font-weight: 600;
    color: #dbdbdb;
  }
  #movie-overview-director {
    padding: 0 1em;
    margin: 2em 0;
  }
  #movie-overview-director h2 {
    font-size: 1.3em;
    font-weight: 700;
    line-height: 0.5em;
  }
  #movie-details-phone h1{
    text-align: center;
    font-size: 2em;
    margin-top: 0.3em;
    font-weight: 700;
  }
  #movie-details-phone span{
    text-align: center;
    margin: 0;
  }
  footer {
    margin-top: 5em !important;
  }
}